import { Injectable } from '@angular/core';
import * as firebase from 'firebase/app';
import 'firebase/auth';

import { User } from '../models/user.model'
import { Subject } from 'rxjs/Subject';

@Injectable()
export class AuthService {

  server: any = firebase;

  user: User = {
    name: 'default',
    email: '',
    uid: "",
    isAuth: false
  };

  userSubject = new Subject<User>();


  constructor() {
    var firebaseConfig = {
      apiKey: "AIzaSyCCUOSbZNmgRAQxbP409hRSQpK8XILxyiQ",
      authDomain: "book-project-b8ab1.firebaseapp.com",
      databaseURL: "https://book-project-b8ab1.firebaseio.com",
      projectId: "book-project-b8ab1",
      storageBucket: "book-project-b8ab1.appspot.com",
      messagingSenderId: "22942401589",
      appId: "1:22942401589:web:ecbbbcb624fc2c6cf81d7c",
      measurementId: "G-08SS9E4W6W"
    };
    // Initialize Firebase
    this.server.initializeApp(firebaseConfig);
  }

  emitUser() {
    this.userSubject.next(this.user);
  }

  isAuth(user: User) {
    this.user = user;
    this.emitUser();
  }

  createNewUser(email: string, password: string, username: string) {
    return new Promise(
      (resolve, reject) => {
        this.server.auth().createUserWithEmailAndPassword(email, password).then(
          () => {
            this.server.auth().currentUser.updateProfile({
              displayName: username
            })
            resolve();
          },
          (error) => {
            console.log(error);
            reject(error);
          }
        )
      }
    )
  }


  signInUser(email: string, password: string) {
    return new Promise(
      (resolve, reject) => {
        this.server.auth().signInWithEmailAndPassword(email, password).then(
          () => {
            resolve();
          },
          (error) => {
            reject(error);
          }
        )
      }
    )
  }

  signOutUser() {
    this.server.auth().signOut();
  }

  getAuth() {
    this.server.auth;
  }


}